import Swal from 'sweetalert2';
const Deleteall = () => {
  // Function should be declared outside the return
  const hnddelete_All = async() => {
const result = await Swal.fire({
    title: 'Are you sure?',
      text: 'This will permanently delete ALL records!',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete all!',
      cancelButtonText: 'Cancel',
})
if(!result.isConfirmed)return;
try{
const url = await fetch('http://localhost:5000/deleteall',{
    method:'DELETE',
    headers:{
        'Content-Type':'application/json',

    }
});
if(url.ok){
    Swal.fire('deleted','success');
}
}catch(err){
console.error('Delete all error:', err);
      Swal.fire('Error', 'Something went wrong.', 'error');
}
};

  return (
    <>
      <h1>DELETE ALL</h1>
  <button
  onClick={hnddelete_All}
  style={{
    backgroundColor: 'skyblue',
    color: 'white',
    padding: '12px 24px',
    border: 'none',
    borderRadius: '6px',
    fontSize: '16px',
    fontWeight: 'bold',
    cursor: 'pointer',
    boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
    transition: 'background-color 0.3s ease',
  }}
  onMouseOver={(e) => (e.target.style.backgroundColor = '#4aaedc')}
  onMouseOut={(e) => (e.target.style.backgroundColor = 'skyblue')}
>
  DELETE RECORDS
</button>

    </>
  );
};

export default Deleteall;
